import React from "react";
export default function Botones(props){
    console.log(props);
    return(
        <>
        <div>
            <button onClick={() => alert(props.m1)} type="button">Modulo 1</button>
            <button onClick={() => alert(props.m2)} type="button">Modulo 2</button>
        </div>
        </>
    )
}